.representativesPage {
  margin: var(--page-margin);
  line-height: 26px;
  font-size: 17px;
}
.searchBox {
  padding: 30px 50px;
  border: 1px lightgray solid;
  border-radius: 3px;
  display: flex;
  justify-content: space-evenly;
}
.searchButton {
  text-align: center;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: darkblue;
  border-radius: 3px;
  padding: 5px 8px;
  transition: 0.3s;
  font-size: 14px;
  box-sizing: border-box;
}

.searchButton:hover {
  cursor: pointer;
  opacity: 0.8;
}
