.contactPage {
  margin: var(--page-margin);
}

.contact-persons-row {
  display: flex;
}

.contact-person {
  margin: 20px 0;
  width: 50%;
}

.contact-name {
  font-weight: 700;
  color: darkblue;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.contact-mail {
  margin: 10px;
}

.col-1 {
  margin-left: 40px;
}
.col-2 {
  margin-right: 40px;
}
.map {
  margin: 50px;
}
.downloads-title {
  margin-top: 60px;
}
.contact-faq {
  margin-top: 60px;
}

.downloads-items {
  display: flex;

  flex-direction: column;
}
.downloads-item {
  line-height: 50px;
  display: flex;

  justify-content: center;
}

.downloads-item > a {
  margin-left: 30px;
}
