.site-header {
  height: 85vh;
  margin-bottom: 70px;
  overflow: hidden;
  position: relative;
  -webkit-box-shadow: 0px 7px 6px -2px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 0px 7px 6px -2px rgba(0, 0, 0, 0.7);
  box-shadow: 0px 7px 6px 2px rgba(0, 0, 0, 0.7);
}
.site-header-small {
  height: 40vh;
  position: relative;
  overflow: hidden;
  margin-bottom: 70px;
  -webkit-box-shadow: 0px 7px 6px -2px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 0px 7px 6px -2px rgba(0, 0, 0, 0.7);
  box-shadow: 0px 7px 6px 2px rgba(0, 0, 0, 0.7);
}

.background-header-pic {
  width: 100%;
  margin: 0 auto;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.35;
}

.background-header-pic-small {
  width: 100%;
  margin: 0 auto;
  z-index: -1;
  position: absolute;

  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  opacity: 0.35;
}

.header-logo-container {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}
.header-logo-container-small {
  position: absolute;
  top: 0;
  left: 0;
  margin: 10px 0 0 70px;
}

.header-logo-box {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-self: center;
}
.header-logo-small {
  width: 200px;
}

.text-under-logo {
  margin-top: 20px;
  margin-left: 20px;
  display: flex;
  justify-self: center;
  align-self: center;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 900;
  letter-spacing: 5px;
  color: black;
  text-shadow: 1px 1px gray;
}
